import { Component, OnInit } from '@angular/core';
import { NavController, Platform, NavParams } from 'ionic-angular';
import { ThemeableBrowser, ThemeableBrowserOptions, ThemeableBrowserObject } from '@ionic-native/themeable-browser';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

@Component({
  	selector: 'page-home',
  	templateUrl: 'home.html',
    providers: [ThemeableBrowser, BarcodeScanner]
})
export class HomePage implements OnInit{
  
  browser: ThemeableBrowserObject;
  urlBeforeClose: string = "";
  
  constructor(
  	public navCtrl: NavController,
    private tb:ThemeableBrowser,
    private platform:Platform,
    private navParams: NavParams,
    private barcodeScanner:BarcodeScanner
  ) {}

  	ngOnInit(){
        this.urlBeforeClose = 'https://www.champion-direct.com';
        this.reopenBrowser();
	 } 

   ionViewDidEnter(){
     alert('IonViewDidEnter');
   }

   reopenBrowser(){
     if(this.isIos()){
        const options: ThemeableBrowserOptions = {
          statusbar: {
            color: '#f53d3d'
          },
          toolbar: {
            height: 44,
            color: '#ffffff'
          },     
          title: {
            color: '#003264ff',
            showPageTitle: true
          },
          backButton: {
            image: 'ic_action_previous_item',
            imagePressed: 'ic_action_previous_item',    
            align: 'left',
            event: 'backPressed'
          },
          forwardButton: {
              image: 'ic_action_next_item',
              imagePressed: 'ic_action_next_item',
              align: 'left',
              event: 'forwardPressed'
          },
          closeButton: {
            image: 'ic_action_remove',
            imagePressed: 'ic_action_remove',
            align: 'left',
            event: 'closePressed'
          },
          backButtonCanClose: true
        };

       this.browser = this.tb.create(this.urlBeforeClose,'_blank', options);
     }else{
       //Possibilité de mettre un loader pour "habiller" la page blanche
        const options: ThemeableBrowserOptions = {
          toolbar: {
            height: 44,
            color: '#ffffff'
          },     
          title: {
            color: '#003264ff',
            showPageTitle: true
          },
          backButton: {
            image: 'ic_action_previous_item',
            imagePressed: 'ic_action_previous_item',    
            align: 'left',
            event: 'backPressed'
          },
          forwardButton: {
              image: 'ic_action_next_item',
              imagePressed: 'ic_action_next_item',
              align: 'left',
              event: 'forwardPressed'
          },
          closeButton: {
            image: 'ic_action_remove',
            imagePressed: 'ic_action_remove',
            align: 'left',
            event: 'closePressed'
          },
          customButtons: [
            {
              image: 'scan',
              imagePressed: 'scan',
              align: 'right',
              event: 'scanButtonPressed'
            }
          ],          
          backButtonCanClose: false //Evite d'avoir la page html qui ne contient rien
         };

       this.browser = this.tb.create(this.urlBeforeClose,'_blank', options);

        this.browser.on('closePressed').subscribe(() => {
          this.platform.exitApp();
        });

        this.browser.on('scanButtonPressed').subscribe((event) => {
          this.openScanner();
          this.urlBeforeClose = event.url;
          this.browser.close();
        });

        /**Evènement ci-dessous à gérer autrement**/
        /*this.browser.on('loadfail').subscribe((event) => {
          alert(JSON.stringify(event));
        })

        this.browser.on('ThemeableBrowserError').subscribe((event) => {
          alert(JSON.stringify(event));
        })*/
     }

   }

   openScanner(){
     this.barcodeScanner.scan().then((result) => {
       if(!result.cancelled)
         this.urlBeforeClose = 'https://www.champion-direct.com/module/ambjolisearch/jolisearch?search_query='+result.text;
       this.reopenBrowser();
     })
   }

   isIos(){
     return this.platform.is('ios');
   }
}



